
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');


*{

    padding :0;
    margin:0;
    box-sizing:border-box;
    font-family: 'Poppins', sans-serif;
}

/* colors and styling */

.root{

   
    --PrimaryColor: hsl(199, 100%, 33%);
--SecondaryColor: hsl(187, 85%, 43%);
--gradientColor: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(46,187,233,1) 35%, rgba(0,212,255,1) 100%);
--whiteColor: hsl(@, @%, 108%);
--blackColor: hsl(201, 33%, 16%);
--textColor: hsl(240, 4%, 36%);
--whiteColorDeam:hsl(@, @%, 93%);
--greyText:hsl(199, 190, 190);
--inputColor:hsl(239, 239, 239);
--bodyColor:hsl(240, 240, 246);
--cardsG:hsl(225, 225, 235);

}


a{

    text-decoration: none;
}

li{

    list-style: none;
}

.section{

    padding:4rem 0 2rem;

}

.container{

    padding-left:1.5rem;
    padding-right:1.5rem;

}

.icon{

    font-size: 2rem;
    cursor: pointer;
}

.flex{

    display:flex;
    align-items: center;
}

.grid{

    display:grid;
    align-items: center;
}

.btn{
    padding:.6rem 1.5rem;
    background: linear-gradient(90deg, rgba(102,195,210,1) 0%, rgba(46,187,233,1) 35%, rgba(0,212,255,1) 100%);
    border:none;
    outline: none;
    cursor: pointer;
    border-radius: 10px;


}

.btn a{
    color:var(--whiteColor);
    font-weight:500;
}

.btn:hover{

    background: var(--SecondaryColor);
}

img,video{

    width:100%;
    height: auto;
}

input{

    font-size: 100%;
}

body{

    background: var(--bodyColor);
}


@media screen and (min-width:1240px) {

    .container{

        width:80% !important;
        margin: auto !important;

    }

    .section{

        padding:0rem 0.4rem;

    }
    
}

@media screen and (min-width:2560px) {

    body{

        zoom:1.7;
    }
}