.root{

    --PrimaryColor: hsl(199, 100%, 33%);
   --SecondaryColor: hsl(187, 85%, 43%);
   --gradientColor: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(46,187,233,1) 35%, rgba(0,212,255,1) 100%);
   --whiteColor: hsl(@, @%, 108%);
   --blackColor: hsl(201, 33%, 16%);
   --textColor: hsl(240, 4%, 36%);
   --whiteColorDeam:hsl(@, @%, 93%);
   --greyText:hsl(199, 190, 190);
   --inputColor:hsl(239, 239, 239);
   --bodyColor:hsl(240, 240, 246);
   --cardsG:hsl(225, 225, 235);
   --cardHover:hsl(240, 7%, 87%);
   
   }


   .footer{

    width:100%;
    position: relative;
    padding:2rem 0;
    display:flex;
    align-items: center;
    justify-content: center;
    margin: auto;

    .videoDiv{

        position: absolute;
        height:100%;
        width:100%;
        

        video{

            height:100%;
            object-fit: cover;

        }
        &::after{

            content:'';
            position: absolute;
            background:rgba(10,84,107,0.307);
            top: 0;
            bottom: 0;
            right: 0;
            left:0;
            mix-blend-mode: multiply;
        }
    }

    .setContent{

        width:100%;
        height:max-content;
        padding:2rem initial;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        margin: auto;
        row-gap: 2rem;
        z-index: 100;


        .contactDiv{

            justify-content: space-between;
            flex-direction: column;
            gap:1rem;

            .text{
                small{

                    font-size:13px;
                    font-weight: 400;
                    color:ghostwhite;

                }
                h2{
                    color:ghostwhite;
                    font-size: 1.8rem;

                }
                &::after{

                    position:relative;
                    content:'';
                    background:#ed6dc6;
                    height:5px;
                    width:120px;
                    right:0;
                    bottom:3px;
                    z-index: -1;
                }

            
            }

            .inputDiv{

                width:100%;
                gap:1rem;
                flex-direction: column;
 
                input{

                    width:100%;
                    padding:-5rem -6rem;
                    border-radius: 3rem;
                    border: 1px solid whitesmoke;
                    outline: none;
                    background: transparent;

                    &::placeholder{

                        color:whitesmoke;
                        opacity:5;
                        text-align: center;
                    }
                }

                .btn{

                    width:80%;
                    justify-content: center;
                    color:white;
                    font-weight:500;
                    gap:-5rem;

                    &:hover{

                        background:#55acde
                    }

                    .icon{

                        font-size: 18px;
                    }
                }
            }
        }

        .footerCard{

            position: relative;
            padding:1rem 1rem 4rem;
            gap:2rem;
            border-radius:1rem;
            background-color: ghostwhite;
            width:100%;
            justify-content: center;
            flex-direction: column;
            align-items: flex-start;
            overflow: hidden;


            .footerIntro{

                flex-basis: 50%;
                justify-content: center;
                align-items: flex-start;
                flex-direction: column;
                row-gap: -5rem;

            }
            .logoDiv{

                .logo{

                    color:black;
                    font-weight: 600;

                    .icon{

                        font-size: 25px;
                        color:#55acde;
                        margin-right: 10px;
                    }
                }
            }

            .footerParagraph{
                font-size:13px;
                color:var(--textColor);
                padding:2px;

            }
            .footerSocials{

                column-gap:-5rem;
                margin: 2px;

                .icon{

                    color:black;
                    font-size: 20px;
                    &:hover{

                        color: #55acde;
                    }
                }
            }

            .footerLinks{

                flex-basis: 50%;
                width:100%;
                gap:1rem;
                grid-template-columns: repeat(2,1fr);
                justify-content: space-between;

                .linkGroup{

                    .groupTitle{

                        display:block;
                        font-weight: 600;
                        color:black;
                       
                    }

                    .footerList{

                        font-size: 15px;
                        color:rgb(24, 24, 25);
                        transition: .3s ease-in-out;

                        .icon{

                            font-size:15px;
                            color:rgb(179, 178, 178);

                        }

                        &:hover{

                            color:#55acde;
                            transform: translateX(7px);

                            .icon{
                                color:#55acde;


                            }
                        }
                    }
                }
            }


        }
       }
   }

   @media screen and (min-width:500px) {

    .footer{
        .setContent{
            .footerCard{

                .footerLinks{

                    grid-template-columns: repeat(2,1fr);
                    padding-bottom: 1rem;
                }
            }
        }
    }
    
   }

   @media screen and (min-width:660px) {

    .footer{
        .setContent{

            .contactDiv{

                flex-direction: row;
                justify-content: space-between;

                .inputDiv{
                    width: max-content;
                    flex-direction: row;
                    justify-content: flex-start;

                    input{

                        width:60%;
                    }
                    .btn{
                        width:max-content;
                    }

                }
            }
           
        }
    }
    
   }

   @media screen and (min-width:840px) {

    .footer{
        
        padding:5rem 0;
        .setContent{
            

            .contactDiv{

                flex-direction: row;
                justify-content: space-between;

                .inputDiv{
                    width: max-content;
                    flex-direction: row;
                    justify-content: flex-start;

                    input{

                        width:60%;
                    }
                    .btn{
                        width:max-content;
                    }

                }
            }
           
        
        .footerCard{

            flex-direction: row;
            .footerLinks{

                grid-template-columns: repeat(2,1fr);
                padding:1rem;

            }
        }}
    }
    
   }

   @media screen and (min-width:1024px) {


    .footer{
        .setContent{

            .footerCard{

                padding:3rem 1rem;

            }

            .footerIntro{

                .footerParagraph{

                    font-size: 15px;
                    color: var(--textColor);
                }

                .footerSocials{

                    column-gap: -4rem;

                    .icon{

                        color:var(--textColor);
                        font-size: 25px;
                        &:hover{
    
                            color: #55acde;
                        }
                    }
                }
            }

            .footerLinks{
                .linkGroup{

                    .footerList{

                        font-size: 16px;
                        padding:5px 0;
                    }
                }
            }
        }
    }
   }