.root{

   
    --PrimaryColor: hsl(199, 100%, 33%);
--SecondaryColor: hsl(187, 85%, 43%);
--gradientColor: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(46,187,233,1) 35%, rgba(0,212,255,1) 100%);
--whiteColor: hsl(@, @%, 108%);
--blackColor: hsl(201, 33%, 16%);
--textColor: hsl(240, 4%, 36%);
--whiteColorDeam:hsl(@, @%, 93%);
--greyText:hsl(199, 190, 190);
--inputColor:hsl(239, 239, 239);
--bodyColor:hsl(240, 240, 246);
--cardsG:hsl(225, 225, 235);

}



.header{

    position: fixed;
    align-items: center;
    justify-content: space-between;
    display: flex;
    background: ghostwhite;
    width:100%;
    padding: 1rem;
    z-index: 1000;
    box-shadow: 0.2px.4px;


    .logo{

        color:var(--blackColor);
        font-weight:600;
        cursor:pointer;
        align-items: center;
        justify-content: center;


        .icon{

            font-size: 25px;
            color:#55acde;
            transform: translateY(5px);
        }


    }

    @media screen and (max-width:768px){

        .navBar{

            position:absolute;
            background: #f1eff0;
            height:max-content;
            width:80%;
            border-radius: 1rem;
            top:-500rem;
            left: 50%;
            transform: translate(-50%);
            z-index: 2000;
            box-shadow:0 2px 4px rgba(43, 44, 44, 0.904);
            transition: .1s ease-in-out;


            .navLists{

                flex-direction: column;
                justify-content: center;
                width:100%;
                margin:auto;
                padding : .5rem 0;
            }

            .navItem{


                padding :.5rem 0 ;
                

                .navLink{

                    color:var(--textColor);
                    font-size:.9rem;
                    font-weight: 600;

                    &:hover{

                        color:var(--PrimaryColor);
                    }



                }
            }
            .btn{

                margin-top: 1rem;

                a{

                    font-weight: 600;
                    background: var(--whiteColor);
                }
            }

            .closeNavbar{

                position: absolute;
                top:1rem;
                right:1.5rem;
                color:#2ebbe9;
                &:hover{
    
                    color:#66c3d2;
                }
            }

            

        }

       
        
    }
.activeNavbar{

    top:7rem;
}


    .toggleNavbar{


       .icon{

        font-size: 25px;
        color:#2ebbe9;

        }

        &:hover.icon{

            color:#66c3d2;
        }
    }
}

@media screen and (min-width:769px){
    .toggleNavbar,.closeNavbar{
        display:none;

    
    }

    .header{
        padding:1.5rem 2rem;

        .navBar{

            .navLists{

                display:flex;
                align-items: center;
                justify-content: center;
                flex-direction: row;

                .navItem{

                    .navLink{

                        color:var(--textColor);
                        padding:0 0.45rem;
                        font-size: 0.9rem;
                        font-weight: 500;

                        &:hover{

                            color:#55acde;
                        }
                    }
                }

                .btn{

                    margin-left: 1rem;

                    a{

                        color:black;
                        font-weight: 600;
                    }
                }
            }
        }
    }

}


@media screen and (min-width:960px)

{
    .header{
        .navBar{
            .navLists{
                .navItem{

                    padding:0 0.7rem;
                }
            }
        }
    }
    
}

@media screen and (min-width:1240px)

{
    .header{
        padding:1rem 0.5rem;
  
    
}

}
