.root{

 --PrimaryColor: hsl(199, 100%, 33%);
--SecondaryColor: hsl(187, 85%, 43%);
--gradientColor: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(46,187,233,1) 35%, rgba(0,212,255,1) 100%);
--whiteColor: hsl(@, @%, 108%);
--blackColor: hsl(201, 33%, 16%);
--textColor: hsl(240, 4%, 36%);
--whiteColorDeam:hsl(@, @%, 93%);
--greyText:hsl(199, 190, 190);
--inputColor:hsl(239, 239, 239);
--bodyColor:hsl(240, 240, 246);
--cardsG:hsl(225, 225, 235);
--cardHover:hsl(240, 96%, 45%);

}


.main{

    width:100%;

    .secTitle{

        .title{

            position:relative;
            width:max-content;
            color:var(--textColor);
            margin:1rem 0;
            z-index: 2;
            

            &::after{

                position:absolute;
                content:'';
                background:#ed6dc6;
                height:5px;
                width:120px;
                right:0;
                bottom:3px;
                z-index: -1;
            }

        }
    }


    .secContent{

        justify-content: center;
        gap:1.5rem;

        .singleDestination{

            height:100%;
            display:grid;
            row-gap:10px;
            border-radius: 10px;
            align-items: center;
            background: var(--cardsG);
            box-shadow: 0 2px 4px rgba(140,140,141,0.549);
            overflow: hidden;


            &:hover{

                background: rgb(191, 188, 188);
                box-shadow: 1 4px 4px rgba(85,85,114,0.549);
                transition: -3s ease;
            }

            .imageDiv{

                height:180px;
                width:100%;
                overflow:hidden;

                img{

                    height:100%;
                    width:100%;
                    object-fit: cover;
                    transition: 2s ease;
                }


                &:hover{

                    img{

                        transform:scale(1.1);
                    }
                }


            }

            .cardInfo{

                padding:1rem;

                .desTitle{

                    color:black;
                    font-size: 1.25rem;
                }

                .continent{

                    gap:-5rem;

                    .icon{

                        color:var(--textColor);
                        font-size: 20px;
                    }

                    .name{

                        color:var(--textColor);
                        font-weight:400;
                        font-size: 14px;
                    
                    }
                }

                .fees{

                    width:100%;
                    justify-content: space-between;
                    padding:1rem;
                    margin:1rem 0;
                    border-top:1.5px solid rgb(170, 168, 168);
                    border-bottom:1.5px solid rgb(170, 168, 168);

                    .grade{

                        color: var(--textColor);
                        max-width:100px;
                        line-height:20px;
                        display:flex;
                        align-items: center;
                        justify-content: center;

                        small{

                            text-align: center;
                            background:rgb(45, 180, 213);
                            border-radius: 1rem;
                            color:ghostwhite;
                            padding:1px 10px;
                            font-size:10px;

                        }
                    }

                    .price{

                        font-size: 2rem;
                        color:var(--blackColor);
                    }
                }

                .description{
                    font-size:13.5px;
                    color:var(--textColor);
                    font-weight:500;
                }

                .btn{

                    margin-top:1rem;
                    color:ghostwhite;
                    font-weight:600;
                    justify-content: space-between;
                    gap:-5rem;


                    .icon{

                        align-self: center;
                        font-size: 18px;
                    }
                }

            }
        }


    }
}

@media screen and (min-width:500px)  {


    .title{

        font-size:1.5rem;
        &::after{

            width:155px !important;
        }
    }
    
    .secContent{

        grid-template-columns: repeat(2,1fr);

        .singleDestination{

            height:auto;
        }
    }
}

@media screen and (min-width:840px)  {
    .secContent{

        grid-template-columns: repeat(3,1fr);

        .singleDestination{

            height:auto;
        }
    }
}


@media screen and (min-width:1240px)  {
.main{

    padding:2rem 1rem;


    .secTitle{
        margin-top:80px;
    }

    .secContent{

        grid-template-columns: repeat(3,1fr);
        margin: auto;
        

        .singleDestination{

            height:auto;

        }
    }
}

}