.root{

   
    --PrimaryColor: hsl(199, 100%, 33%);
--SecondaryColor: hsl(187, 85%, 43%);
--gradientColor: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(46,187,233,1) 35%, rgba(0,212,255,1) 100%);
--whiteColor: hsl(@, @%, 108%);
--blackColor: hsl(201, 33%, 16%);
--textColor: hsl(240, 4%, 36%);
--whiteColorDeam:hsl(@, @%, 93%);
--greyText:hsl(199, 190, 190);
--inputColor:hsl(239, 239, 239);
--bodyColor:hsl(240, 240, 246);
--cardsG:hsl(225, 225, 235);

}

.home{

    width:100%;
    position: relative;
    display:flex;
    align-items: center;
    justify-content: center;
    margin: auto;

    .overlay{

        position: absolute;
        height:100%;
        width: 100%;
        background: rgb(255, 255, 255);
        top:0;
        bottom: 0;
        z-index: 1;
        mix-blend-mode:color-burn;
        margin-top: 70px;
        

    }

    video{
        position: absolute;
        height:100%;
        width: 100%;
        top:0;
        bottom: 0;
        object-fit: cover;
        margin-top: 70px;


    }

    .homeContent{

        width:100%;
        height:max-content;
        padding:8rem 1.5rem 3rem;
        display: flex;
        row-gap: 3rem;
        flex-direction: column;
        align-items: stretch;
        justify-content: center;
        margin: auto;
        color: #000000;
        z-index: 100;
        margin-top:100px;

    }

    .textDiv{

        padding:2rem 0;

        .smallText{

            font-size: 13px;
            text-transform: uppercase;


        }

        .homeTitle{

            position: relative;
            font-size: 1.8rem;
        }
    }

    .cardDiv{

        position: relative;
        padding:2rem 1rem;
        background: #ffffff;
        border-radius: 10px;
        gap:1rem;


        label{

            display: block;
            color: #0c0c0c;
            padding-bottom: 10px;
            font-size: 13px;
            font-weight: 500;

        
        }

        .label_total{

            justify-content: space-between;
            align-items: center;

            .total{

                color:#0c0c0c;
                font-size: 18px;
                padding-bottom: 10px;
                
            }

           

            
           
    }

  
  
}

.btn{

    font-size: 10px;
    font-weight: 500;
    width:30%;
   margin-left:100px;


  
    
    
}

.homeFooterIcons{

    width:100%;
    justify-content:space-between;

    .icon{

        font-size: 20px;
        margin:0 5px;
        color:#ffffff;
        font-weight: 600;

        &:hover{

            color: #0c0c0c;
        }
    }
}

}


@media screen and (min-width:500px) {


    .home{
        .homeContent{
            .textDiv{
                .smallText{

                    font-size: 16px;
                }
                .homeTitle{
                    position: relative;
                    font-size: 2.3rem;
                    width:max-content;


                    &::after{

                        position: absolute;
                        content: '';
                        background: #ed6dc6;
                        height:5px;
                        width:145px;
                        right:0px;
                        bottom: 5px;
                        z-index:-1
                    }

                }
            }

            .cardDiv{
                grid-template-columns: repeat(2,1fr);

            }
        }
        
        .btn{

            font-size: 10px;
            font-weight: 500;
            width:30%;
            margin:auto;
        
        
          
            
            
        }
    }
    
}
@media screen and (min-width:660px) {

    .home{
        .homeContent{
            .cardDiv{
                padding:2rem 1rem;
                grid-template-columns: repeat(3,1fr);

            }
        }
        .btn{

            font-size: 10px;
            font-weight: 500;
            width:30%;
            margin:auto;
        
        
          
            
            
        }
    }
}

@media screen and (min-width:960px) {

    .home{
        height:80vh;

        .btn{

            font-size: 10px;
            font-weight: 500;
            width:30%;
            margin:auto;
        
        
          
            
            
        }
      
    }
}

@media screen and (min-width:1000px) {

    .home{
        height:110vh;

        .homeContent{

          

            .cardDiv{
                

                padding:2rem 1rem;
                grid-template-columns: repeat(3,1fr);
                .input{
                    margin-bottom: -1rem;
                }
            }
      
        }

        .btn{

            font-size: 13px;
            font-weight: 500;
            width:20%;
           margin:auto;
           color:white;
        
        &:hover{

            background: #b0e3f8;
            color:black
        }
        
          
            
            
        }
      
    }
}

